import supabase from "./supabase.js";





// 
const supaAPI = async () => {
    const {
        data: user
    } = await supabase
        .from('user')
        .select('*')
    return user;
}

// 
const insetAPI = async (r) => {
    const {
        data
    } = await supabase
        .from('user')
        .insert(r)
        .select()
    return data
}

// 
const selAPI = async (n) => {
    const {
        data
    } = await supabase
        .from('user')
        .select('*')
        .eq('Byid', n)
        .single();
    console.log('');

    return data
}

// 
const delAPI = async d => {
    const {
        data,
        error
    } = await supabase
        .from('user') // 
        .delete() // 
        .eq('Byid', d); //  id  1 
}

//  id  1  email 
const updataAPI = async (up, newdate) => {
    const {
        data,
        error
    } = await supabase
        .from('user') // 
        .update(newdate)
        .eq('Byid', up) // id  1  
        .select() //  

    return data
}




// Api

// 
const selArtAPI = async (Byid) => {
    const {
        data
    } = await supabase
        .from('articles')
        .select('*')
        .eq('author_Byid', Byid)

    return data
}


// 
const selOneAPI = async (art_uuid) => {
    const {
        data
    } = await supabase
        .from('articles')
        .select('*')
        .eq('art_uuid', art_uuid)
        .single();
    console.log(data, '');

    return data
}

// 
const DelartAPI = async (art_uuid, author_Byid) => {
    try {
        await supabase
            .from('articles') // 
            .delete() // 
            .eq('art_uuid', art_uuid) //  id  1     

        // 
        const {
            data,
            error
        } = await supabase.from('articles').select('*').eq('author_Byid', author_Byid);
        if (error) {
            throw error;
        }
        return data;
    } catch (error) {
        console.error('Error deleting and fetching remaining articles:', error);
        return null;
    }
}

// 
const adArtAPI = async (title, content, DateTime, articleId) => {

    // const articleId = this.$route.params.id;  
    const {
        data,
        error
    } = await supabase
        .from("articles")
        .insert([{
            title: title,
            content: content,
            created_at: DateTime,
            author_Byid: articleId,
            image: 'https://img.daisyui.com/images/stock/photo-1635805737707-575885ab0820.webp',
        }])

    if (error) {
        console.error('', error);
    } else {

        console.log('', data);
        return 'ok'
    }


    //   .eq("author_Byid", articleId);
}

// 
const ybdata = async (key) => {
    const {
        data
    } = await supabase
        .storage
        .from('yb') //  Supabase 
        .getPublicUrl(`diagrammatic/${key}.png`); //  URL     
    return data
}

// 
const ybdatamp = async (key) => {
    const {
        data
    } = await supabase
        .storage
        .from('yb') //  Supabase 
        .getPublicUrl(`Articulation/${key}.MP3`); //  URL     
    return data
}


export {
    supaAPI,
    insetAPI,
    selAPI,
    delAPI,
    updataAPI,
    selArtAPI,
    selOneAPI,
    DelartAPI,
    adArtAPI,
    ybdata,
    ybdatamp
}